* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

*::-webkit-scrollbar {
	width: 10px;
	background: rgba(0,0,0,0.3);
}

*::-webkit-scrollbar-track {
	width: 6px;
	border-radius: 5px;
}

*::-webkit-scrollbar-thumb{
	background: rgba(0,0,0,0.3);
	border-radius: 5px;
}
*::-webkit-scrollbar-button {
}

body {
	background: #333;
	font-family: segoe UI;
	height: 100vh;
	overflow: scroll;
}

.container {
	width: 320px;
	margin: 5% auto;
	min-height: 200px;
	position: relative;
	padding: 1em;
	-webkit-justify-content: center;
	        justify-content: center;
}

.card {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-areas: 
	'img title title title'
	'img calc calc calc';
	grid-gap: 10px;
	background: #fff;
	width: 100%;
	padding: 1em;
	border-radius: 3px;
	margin: 1.5em 0px;
	display: none;
	cursor: pointer;
	opacity: 0.9;
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-transition: all 0.6s;
	transition: all 0.6s;
	min-height: 80px;
	max-height: 150px;
	overflow-y: scroll; 
	word-wrap: break-word;
}

.img {
	width: 45px;
	height: 45px;
	background: #5566bb;
	align-self: center;
	border-radius: 50%;
	grid-area: img;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	box-shadow: 0px 2px 5px rgba(0,0,0,0.3);
}

.title {
	font-size: 1em;
	font-weight: 600;
	letter-spacing: 2px;
	grid-area: title;
}

.calc {
	text-align: left;
	grid-area: calc;
}

.orange {
	background: #99dd55;
	color: #fff;
}

.blue {
	background: steelblue;
	color: #fff;
	opacity: 0.9;
}

.green {
	background: #55BB66;
	color: #fff;
}

.red {
	background: #dd5855;
	color: #fff;
	opacity: 0.9;
}

.display {
	display: grid;
}

.hide {
	display: none;
}

.card:hover {
	-webkit-transform: scale(1.3);
	        transform: scale(1.3);
}

.card:after {
	content: '';
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	position: absolute;
	background: rgba(0,0,0,0.081);
	z-index: 10;
	-webkit-transition: 0.7s;
	transition: 0.7s;
	-webkit-transform: translate(-70%) skew(50deg, 0deg);
	        transform: translate(-70%) skew(50deg, 0deg);
	box-shadow: 0px 1px 5px rgba(0,0,0,0.1);
}

.card:hover:after {
	-webkit-transform: translate(0%);
	        transform: translate(0%);
}
